import React from "react";
import { Gender } from "./Catagory/Gender";
import { Species } from "./Catagory/Species";
import { Status } from "./Catagory/Status";
const Filters = ({setPageNumber, setStatus, setSpecies, setGender}) => {
  let clear = ()=>{
    setPageNumber(1);
    setStatus('');
    setGender('');
    setSpecies('');
    window.location.reload(false);
  }
  return (
    <div className="col-lg-3 col-12 mb-3">
      <div className="text-center fw-bold fs-4 mb-2">Filter</div>
      <div
      onClick={clear}
        style={{ cursor: "pointer" }}
        className="text-center text-primary text-decoration-underline my-4"
      >
        Clear Filters
      </div>

      <div className="accordion" id="accordionExample">
        <Status setStatus={setStatus} setPageNumber={setPageNumber}/>
        <Species setSpecies={setSpecies} setPageNumber={setPageNumber} />
        <Gender setGender={setGender} setPageNumber={setPageNumber}/>
      </div>
    </div>
  );
};

export default Filters;
