import React from 'react'

export const Search = ({ setPageNumber, setSearch }) => {
  return (
    <form id='search-box' className='d-flex  justify-content-center fs-5 gap-4 mb-5'>
        <input onChange={(e)=>{
            setPageNumber(1);
            setSearch(e.target.value);
        }} id='search' placeholder="Search for characters" />
        <button className='btn search-button btn-primary'>Search</button>
    </form>
  )
}
