import React from 'react'
import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom'
const CardDetails = () => {
    let {id} = useParams();
    const [fetchedData, updateFetchedData] = useState([]);
    let {name, location, image, origin, gender, species, type, status} = fetchedData;
    let api =  `https://rickandmortyapi.com/api/character/${id}`

    useEffect(()=>{
        (async function(){
            let data = await fetch(api).then((response)=> response.json());
            updateFetchedData(data)
        })()
    }, [api])
  return (
    <div className="container  d-flex justify-content-center">
       <div className="d-flex flex-column gap-3">
       <h1 className="text-center text-primary">{name}</h1>
        <img src={image} alt="" className='img-fluid' />
        {(() => {
            if (status === "Dead") {
              return <div className="btn btn-danger fs-6">{status}</div>;
            } else if (status === "Alive") {
              return <div className="btn btn-success fs-6">{status}</div>;
            } else {
              return <div className="btn btn-secondary fs-6">{status}</div>;
            }
          })()}
           <div className="content ">
        <div className="">
            <span className="fw-bold">Species:</span>
            {species}
        </div>
        <div className="">
            <span className="fw-bold">Location:</span>
            {location?.name}
        </div>
        <div className="">
            <span className="fw-bold">Origin:</span>
            {origin?.name}
        </div>
        <div className="">
            <span className="fw-bold">gender:</span>
            {gender}
        </div>
        <div className="">
            <span className="fw-bold">Type:</span>
            {type}
        </div>
       </div> 
       </div>
      
    </div>
  )
}

export default CardDetails