import React, {useState, useEffect} from 'react'
import ReactPaginate from 'react-paginate'

export const Pagination = ({info, setPageNumber, pageNumber}) => {
  const [width, setWidth] = useState(window.innerWidth);
  let updateWidth= () =>{
    setWidth(window.innerWidth)
  }
  useEffect(()=>{
    window.addEventListener('resize', updateWidth);
    return ()=> window.removeEventListener('resize', updateWidth)
  },[])
  return (
    <>
    <style jsx>
      {`
      @media (max-width:680px){
        .next,
        .prev{
          display:none;
        }
      }
      `}
    </style>
    <ReactPaginate
    previousLabel="Prev"
    className='d- pagination justify-content-center gap-4 my-4'
    pageClassName='page-item'
    forcePage={pageNumber===1? 0 : pageNumber= -1}
    pageLinkClassName='page-link'
    previousClassName='page-buttons prev'
    nextClassName='page-buttons next'
    previousLinkClassName='page-buttons-link'
    nextLinkClassName='page-buttons-link'
    activeClassName="active"
    pageRangeDisplayed={width<576? 1: 2}
    marginPagesDisplayed={width< 576? 1 :2}
    pageCount={info?.pages}
    onPageChange={
      (data)=>{
      setPageNumber(data.selected +1);
      }
    }
/></>
    
  )
}

