import React  from "react";
import { Link } from "react-router-dom";
const Cards = ({ results, pages }) => {
  let display;
  if (results) {
    display = results.map((data) => {
      let { id, name, image, location, status } = data;
      
      return (
        <Link to={`${pages}${id}`} key={id} className="col-lg-4 col-12 mb-5 position-relative">
          <div className="rick-card">
            <img src={image} alt="" className="img" />
            <div className="content">
              <div id="name" className="fs-5 fw-bold mb-4">
                {name}
              </div>
              <div className="">
                <div className="fs-7">Last location</div>
                <div className="fs-6">{location.name}</div>
              </div>
            </div>
          </div>
          {(() => {
            if (status === "Dead") {
              return <div className="badge bg-danger fs-6">{status}</div>;
            } else if (status === "Alive") {
              return <div className="badge bg-success fs-6">{status}</div>;
            } else {
              return <div className="badge bg-secondary fs-6">{status}</div>;
            }
          })()}
        </Link>
      );
    });
  } else {
    display = "result not found :/";
  }
  return <>{display}</>;
};

export default Cards;
