import React from 'react'
import Cards from '../components/Cards'
import InputGroup from '../components/Filters/Catagory/InputGroup'
import { useState, useEffect } from 'react'

const Episodes = () => {
  const [id, setId] = useState(1);
  const [info, setInfo] = useState([]);
  const[results, SetResults] = useState([]);
  const {air_date, name}= info;
  console.log(info);

  let api = `https://rickandmortyapi.com/api/episode/${id}`;

  useEffect(()=>{
  (async function(){
      let data = await fetch(api).then((response)=> response.json())
      setInfo(data);
    
      let cardData = await Promise.all(
        data.characters.map((everyCharacterApiUrl)=>{
          return fetch(everyCharacterApiUrl).then((response)=> response.json());
        })
      )
      SetResults(cardData);

    })()
  },[api])
  return (
    <div className="container">
      <div className="row mb-4">
        <h1 className="text-center">
          Episode: 
          <span className="text-primary">{name===""? "Unknown" : name}</span>
        </h1>
        <h5 className="text-center">
          Air Date: {air_date==="" ? "Unknown" : air_date}
        </h5>
      </div>
      <div className="row">
        <div className="col-lg-3 col-12 mb-4">
          <h4 className="text-center mb-4">Pick Episodes</h4>
          <InputGroup inputName="Episode" setId={setId} total={51}/>
        </div>
        <div className="col-8">
          <div className="row">
            <Cards pages="/episodes/" results={results}/>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Episodes;