import React from 'react'
import Cards from '../components/Cards'
import InputGroup from '../components/Filters/Catagory/InputGroup'
import { useState, useEffect } from 'react'

const Location = () => {
  const [id, setId] = useState(1);
  const [info, setInfo] = useState([]);
  const[results, SetResults] = useState([]);
  const {name, type, dimension}= info;
  console.log(info);

  let api = `https://rickandmortyapi.com/api/location/${id}`;

  useEffect(()=>{
  (async function(){
      let data = await fetch(api).then((response)=> response.json())
      setInfo(data);
    
      let cardData = await Promise.all(
        data.residents.map((everyCharacterApiUrl)=>{
          return fetch(everyCharacterApiUrl).then((response)=> response.json());
        })
      )
      SetResults(cardData);

    })()
  },[api])
  return (
    <div className="container">
      <div className="row mb-4">
        <h1 className="text-center">
          Location: <span className="text-primary">{name===""? "Unknown" : name}</span>
        </h1>
        <h5 className="text-center">
           Dimension: {dimension==="" ? "Unknown" : dimension}
        </h5>
        <h6 className="text-center">
           Type: {type==="" ? "Unknown" : type}
        </h6>
      </div>
      <div className="row">
        <div className="col-lg-3 col-12 mb-4">
          <h4 className="text-center mb-4">Pick Location</h4>
          <InputGroup setId={setId} inputName="Location" total={126}/>
        </div>
        <div className="col-8">
          <div className="row">
            <Cards pages="/location/" results={results}/>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Location;