import React from "react";

export const FilterBtn = ({ name, items, index, task, setPageNumber, species }) => {
  return (
    <div>
      <style jsx>
        {`
        .x:checked + label {
          background-color:#0d6efd;
          color:white;
        }
        input[type='radio']{
          display:none;
        }
        `}
      </style>
      <div className="form-check">
        <input
          className="form-check-input x"
          type="radio"
          name={name}
          id={`${name}-${index}`}
          onChange={()=>{
            setPageNumber(1);
            task(items);
          }}
        />
        <label className="btn btn-outline-primary" for={`${name}-${index}`}>
          {items}
        </label>
      </div>
    </div>
  );
};
